.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;

    grid-template-areas:
"h h"
"n c";
    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
  grid-area: c;
  background-color: darkred;
}

.app-wrapper-content img {
  width: 800px;
}

